:root{

  --ff_p:'Euclid Circular B', sans-serif;
  --ff_s:'Archia', sans-serif;
  --ff_t:'Playfair Display', serif;

  --c_1:hsl(0 0% 6%);
  --c_2:hsl(0 0% 98%);
  --c_3:hsl(0 0% 60%);
  --c_4:hsl(0 0% 12%);

  --c_10:#FFCC00;
  --c_11:#D60081;
  --c_12:#8000FF;
  --c_13:#B3FF00;

}

::selection{
  background-color:hsl(78 100% 50% / 0.2);
  color:white;
}

html::-webkit-scrollbar{
  width:0;
  height:0;
}

html{
  -webkit-tap-highlight-color:transparent;
  scroll-behavior:smooth;
}

body{
  margin:0;
  background-color:var(--c_1);
  color:var(--c_2);
  font-family:var(--ff_s);
  font-weight:400;
  font-size:16px;
}

main{
  width:100%;
  overflow:hidden;
}

main > section{
  padding:0 128px;
  box-sizing:border-box;
}

h1, h2, h3{
  user-select: none;
  margin:0;
  font-family:var(--ff_p);
}

h2.h2, h3.h3{
  font-weight:400;
  text-transform:capitalize;
}

h2.h2 > span, h3.h3 > span{
  font-weight:700;
}

h2.h2{
  font-size:44px;
  line-height:44px;
}

h3.h3{
  font-size:18px;
  line-height:18px;
}

p > em{
  font-family:var(--ff_t);
}

.shape-rect::after{
  content:'';
  width:48px;
  height:48px;
  border:2px solid white;
  position:absolute;
  z-index:-1;
}

.shape-line::after{
  content:'';
  width:48px;
  height:2px;
  background-color:white;
  position:absolute;
  z-index:-1;
}

[class*="remixicon"]:is(span, i){
  --size:20;
  --color:var(--c_2);
}



@media only screen and (min-width:768px) and (max-width:1024px) and (orientation:portrait){

  main > section{
    padding:0 64px;
  }

}


@media only screen and (max-width:767px) and (orientation:portrait){

  html.stop-scrolling{
    height:100%;
    overflow:hidden;
  }

  main > section{
    padding:0 32px;
  }

  h2.h2{
    font-size:24px;
    line-height:24px;
  }

  h3.h3{
    font-size:12px;
    line-height:12px;
  }

  .shape-rect::after{
    width:40px;
    height:40px;
  }

}