section.about-me{
  padding-top:128px;
  padding-bottom:64px;
}

section.about-me > h2{
  position:relative;
}

section.about-me > h2::after{
  border-color:var(--c_11);
  left:-12px;
  top:-12px;
  transform:rotate(20deg);
}

section.about-me > p{
  margin:64px 0;
  font-size:32px;
  line-height:calc(32px * 1.5);
  padding:0 128px;
  box-sizing:border-box;
}

section.about-me > h3{
  width:fit-content;
  letter-spacing:0.4px;
  position:relative;
}

section.about-me > h3::after{
  background-color:var(--c_10);
  bottom:-4px;
  right:-12px;
}

.skills-box{
  margin:64px 0 0;
  display:grid;
  grid-template:repeat(2, auto) / repeat(3, auto);
  justify-content:center;
  align-items:center;
  flex-wrap:wrap;
  gap:64px 128px;
}

.skills-box > div > span{
  display:block;
}

.skills-box > div > span:nth-child(1){
  font-size:24px;
}

.skills-box > div > span:nth-child(2){
  user-select: none;
  margin-top:4px;
  font-size:12px;
  color:var(--c_3);
  font-family:var(--ff_p);
}


@media only screen and (min-width:768px) and (max-width:1024px) and (orientation:portrait){

  section.about-me > p{
    padding:0 64px;
  }

  .skills-box{
    gap:64px 96px;
  }

}


@media only screen and (max-width:767px) and (orientation:portrait){

  section.about-me > p{
    margin:32px 0;
    padding:0;
    font-size:16px;
    line-height:calc(16px * 1.5);
  }

  .skills-box{
    margin-top:32px;
    grid-template:repeat(3, auto) / repeat(2, auto);
    gap:32px 64px;
  }

  .skills-box > div > span:nth-child(1){
    font-size:16px;
  }

}