.preloader{
  height:100vh;
  width:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  box-sizing:border-box;
  position:fixed;
}

.preloader ~ *{
  visibility:hidden;
}

.preloader > .loader{
  height:24px;
  width:240px;
  background-color:var(--c_4);
  position: relative;
}

.preloader > .loader.blinking{
  animation:blinking 500ms ease 0s infinite;
}

.preloader > .loader::after{
  content:'';
  height:inherit;
  width:100%;
  background-color:var(--c_2);
  position:absolute;
  top:0;
  left:0;
  animation:width 1s linear 0s forwards;
  animation-play-state:paused;
}

.preloader > .loader.anim-run::after{
  animation-play-state:running;
}


@keyframes width{
  0%{
    width:0;
    background-color:var(--c_10);
  }
  50%{
    width:50%;
    background-color:var(--c_11);
  }
  100%{
    width:100%;
    background-color:var(--c_12);
  }
}

@keyframes blinking{
  0%{
    opacity:0;
  }
  100%{
    opacity:1;
  }
}