.nav-bar{
  height:64px;
  width:fit-content;
  padding:8px 64px 8px 8px;
  box-sizing:border-box;
  position:fixed;
  top:0;
  right:0;
  z-index:20;
}

.nav-list{
  user-select: none;
  margin:0;
  padding:0;
  height:100%;
  display:flex;
  flex-direction:row;
  justify-content:center;
  align-items:center;
  gap:16px;
  box-sizing:border-box;
}

.nav-list > li{
  cursor: pointer;
  display:inline-block;
  font-family:var(--ff_p);
  text-transform:capitalize;
  background-color:var(--c_1);
  padding:4px 8px;
  position: relative;
}

.nav-list > li::after{
  --size:0px;
  --color:transparent;
  content:'';
  height:var(--size);
  width:var(--size);
  background-color:var(--color);
  box-shadow:0 0 12px 1px var(--color);
  border-radius:50%;
  box-sizing:border-box;
  position:absolute;
  bottom:-50%;
  right:36%;
  transform:translate(-50%, 50%);
}

.nav-list > li.active::after{
  --size:6px;
}

.nav-list > li[name='home'].active::after{
  --color:var(--c_2);
}

.nav-list > li[name='about-me'].active::after{
  --color:var(--c_11);
}

.nav-list > li[name='projects'].active::after{
  --color:var(--c_12);
}

.nav-list > li[name='contact-me'].active::after{
  --color:var(--c_10);
}

.nav-bar > button{
  display:none;
}


@media only screen and (max-width:767px) and (orientation:portrait){

  .nav-bar{
    padding-right:16px;
  }

  .nav-bar > button{
    display:inline-block;
    height:48px;
    width:48px;
    background-color:var(--c_1);
    border:none;
    box-sizing:border-box;
  }

  .nav-list{
    width:100%;
    flex-direction:column;
    align-items:flex-start;
    background-color:var(--c_1);
    padding:0 32px;
    box-sizing:border-box;
    position:fixed;
    top:0;
    right:-100%;
    z-index:-1;
    transition:right 350ms linear;
  }

  .nav-list.show{
    right:0%;
  }

  .nav-list > li{
    width:100%;
    padding-left:32px;
    font-size:32px;
    box-sizing:border-box;
  }

  .nav-list > li::after{
    bottom:38%;
    right:94%;
    transform:translate(0, 0);
  }

  .nav-list > li.active::after{
    --size:12px;
  }

}