.css-pattern-1{
  --c_1:hsl(0 0% 6%);
  --c_2:hsl(0 0% 12%);
  --s:20px;
  --_g:var(--c_1) 90deg, #0000 90.5deg;
  background:conic-gradient(from -45deg, var(--_g)), conic-gradient(from 135deg, var(--_g)) calc(var(--s)/2) 0, var(--c_2);
  background-size:var(--s) var(--s);
}

.css-pattern-2{
  --c_2:hsl(0 0% 6%);
  --c_1:hsl(0 0% 12%);
  background-image:repeating-radial-gradient(circle at 0 0, transparent 0, var(--c_1) 20px), repeating-linear-gradient(var(--c_2), var(--c_2));
}

.css-pattern-3{
  --c_1:hsl(0 0% 12%);
  background-image:radial-gradient(var(--c_1) 2px, transparent 0px);
  background-size:12px 12px;
}

section.projects{
  counter-reset:project;
  padding-top:64px;
  padding-bottom:64px;
}

section.projects > h2{
  position:relative;
}

section.projects > h2::after{
  border-color:var(--c_12);
  left:-12px;
  top:-12px;
  transform:rotate(20deg);
}

.project-box{
  margin-top:64px;
  display:flex;
  flex-direction:column;
  box-sizing:border-box;
}

.project-box > *{
  display:block;
}

.project-box > span.title{
  font-size:32px;
  font-family:var(--ff_p);
  text-transform:capitalize;
}

.project-box > span.title::before{
  counter-increment:project;
  content:counter(project) ". ";
  font-family:var(--ff_t);
  font-weight:700;
  font-style:italic;
}

.project-box > span.title > a{
  margin-left:16px;
  display:inline-flex;
  background-color:var(--c_4);
  padding:2px;
  transform:translateY(10%);
}

.project-box > span.title > a > span{
  --color:var(--c_13);
}


.project-box > span.title + span{
  user-select: none;
  margin-left:30px;
  margin-top:4px;
  font-size:12px;
  color:var(--c_3);
  font-family:var(--ff_p);
}

.project-box > div.img-box{
  margin-top:32px;
  margin-left:calc(30px + 128px + 128px + 128px);
  width:80%;
  aspect-ratio:3/2;
  overflow:hidden;
}

.project-box > div.img-box > img{
  user-select: none;
  width:100%;
  height:auto;
  aspect-ratio:3/2;
  filter:grayscale(1);
  transform:translate(64px, 64px);
  transition:filter 350ms linear;
}

.project-box > div.img-box > img:hover{
  filter:grayscale(0);
}


@media only screen and (min-width:768px) and (max-width:1024px) and (orientation:portrait){

  .project-box > div.img-box{
    margin-left:calc(30px + 128px + 64px);
  }

}


@media only screen and (max-width:767px) and (orientation:portrait){

  .project-box{
    margin-top:32px;
  }

  .project-box > span.title{
    margin-top:8px;
    font-size:20px;
  }

  .project-box > span.title > a{
    padding:0;
    transform:translateY(20%);
  }

  .project-box > span.title + span{
    margin-left:0;
  }

  .project-box > div.img-box{
    margin-top:0;
    margin-left:0;
    width:100%;
    order:-1;
  }

  .project-box > div.img-box > img{
    transform:translate(32px, 32px);
  }


}