section.contact-me{
  padding-top:64px;
  padding-bottom:64px;
  display:flex;
  flex-direction:row;
  justify-content:space-between;
}

section.contact-me > h2{
  position:relative;
}

section.contact-me > h2::after{
  border-color:var(--c_10);
  left:-12px;
  top:-12px;
  transform:rotate(20deg);
}

.contacts-list{
  margin:0;
  margin-right:128px;
  padding:0;
  display:flex;
  flex-direction:row;
  justify-content:center;
  align-items:center;
  gap:32px;
}

.contacts-list > li{
  display:inline-block;
  padding:0;
}

.contacts-list > li > a{
  font-size:0;
  text-decoration:none;
  display:block;
  padding:6px;
  transition:transform 350ms linear;
}

.contacts-list > li > a > span{
  --size:32;
}

.contacts-list > li > a:hover{
  transform:translateY(-6px);
}

.contacts-list > li > a:hover > span{
  --color:var(--c_13);
}


@media only screen and (min-width:768px) and (max-width:1024px) and (orientation:portrait){

  .contacts-list{
    margin-right:64px;
  }

}


@media only screen and (max-width:767px) and (orientation:portrait){

  section.contact-me{
    padding-bottom:0;
    flex-direction:column;
    gap:32px;
  }

  .contacts-list{
    margin:0 auto;
    gap:16px;
  }

  .contacts-list > li > a > span{
    --size:24;
  }

}