section.home{
  height:100vh;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
}

section.home > h1{
  margin:0;
  font-size:72px;
  line-height:72px;
  font-weight:400;
  letter-spacing:1px;
  text-align:center;
  text-transform:uppercase;
}

section.home > h1 > span{
  font-weight:700;
}

section.home > p{
  margin:0;
  margin-top:32px;
  width:32ch;
  font-size:16px;
  line-height:calc(16px * 1.5);
  text-align:center;
}

section.home > span:nth-child(3){
  user-select: none;
  margin-top:64px;
  font-size:8px;
  font-family:var(--ff_p);
  letter-spacing:2px;
  text-transform:uppercase;
  color:var(--c_3);
}

section.home > span:nth-child(4){
  margin:4px;
  --size:24;
  animation-name:down;
  animation-duration:1s;
  animation-timing-function:ease;
  animation-delay:0s;
  animation-iteration-count:infinite;
}

section.home > span.sticker{
  user-select: none;
  font-size:10px;
  font-family:var(--ff_t);
  font-style:italic;
  line-height:18px;
  padding:0 12px;
  position:absolute;
}

section.home > span.sticker.s1{
  background-color:var(--c_10);
  color:var(--c_1);
  transform:translate(128px, -200px) rotate(20deg);
}

section.home > span.sticker.s2{
  background-color:var(--c_11);
  color:var(--c_2);
  transform:translate(-240px, 50px) rotate(-20deg);
}

section.home > span.sticker.s3{
  background-color:var(--c_12);
  color:var(--c_2);
  transform:translate(196px, 100px) rotate(20deg);
}


@keyframes down{
  0%{
    opacity:1;
    transform:translateY(0) scale(1);
  }
  100%{
    opacity:0;
    transform:translateY(32px) scale(1);
  }
}



@media only screen and (max-width:767px) and (orientation:portrait){
  
  section.home > h1{
    font-size:32px;
    line-height:32px;
  }

  section.home > p{
    margin-top:16px;
    font-size:12px;
    line-height:calc(12px * 1.5);
  }

  section.home > span:nth-child(3){
    margin-top:32px;
  }

  section.home > span.sticker.s1{
    transform:translate(64px, -160px) rotate(20deg);
  }

  section.home > span.sticker.s2{
    transform:translate(-64px, 160px) rotate(-20deg);
  }

  section.home > span.sticker.s3{
    transform:translate(96px, 100px) rotate(20deg);
  }

}
